import io from "socket.io-client";

import "bootstrap/dist/css/bootstrap.css";
import "./style.css";
import React from "react";
import ReactDOM from "react-dom";
import { useEffect, useState } from "react";
import moment from "moment";

const username = prompt("what is your username");
// ny l a ch ip or domain ca server
const socket = io("http://localhost:3000", {
  transports: ["websocket", "polling"],
});

const App = ({}) => {
  const [users, setUsers] = useState([]);
  const [message, setMessage] = useState("");
  const [messages, setMessages] = useState([]);

  useEffect(() => {
    // Bc 1: gi username user va nhp ln cho server
    socket.on("connect", () => {
      socket.emit("username", username);
    });
    // Bc 2: server s respon v cho client trng thi kt ni, nhn data respon t server vs key l "connected"
    socket.on("connected", (user) => {
      console.log(user);
    //  setUsers((users) => [...users, user]);
    });
    // Bc 3: nhn data respon t server vs key l "users"
    socket.on("users", (users) => {
      console.log(users);
      // mi username gi ln server, server s lu li di dng array v respon v cho client -> 1 user l 1 obj trong array
      setUsers(users);
    });
    // Bc 5: nhn data respon t server vs key l "message"
    socket.on("message", (message) => {
      console.log(message);
      // server respon object message v cho client, mi ln server respon vy client lu li vo mng
      setMessages((messages) => [...messages, message]);
    });
    // Bc 6: nhn data respon t server vs key l "disconnected"
    socket.on("disconnected", (id) => {
      console.log(id)
      // mi ln user no tt tab browser (disconnection) th server s respon id ca user 
      setUsers((users) => {
        return users.filter((user) => user.id !== id);
      });
    });
  }, []);
  const submit = (event) => {
    event.preventDefault();
    // Bc 4: gi message input ln cho server
    socket.emit("send", message);
    setMessage("");
  };

  return (
    <div className="container">
      <div className="row">
        <div className="col-md-12 mt-4 mb-4">
          <h6>Hello {username}</h6>
        </div>
      </div>
      <div className="row">
        <div className="col-md-8">
          <h6>Messages</h6>
          <div id="messages">
            {messages.map(({ user, date, text }, index) => (
              <div key={index} className="row mb-2">
                <div className="col-md-3">
                  {moment(date).format("h:mm:ss a")}
                </div>
                <div className="col-md-2">{user.name}</div>
                <div className="col-md-2">{text}</div>
              </div>
            ))}
          </div>
          <form onSubmit={submit} id="form">
            <div className="input-group">
              <input
                type="text"
                className="form-control"
                onChange={(e) => setMessage(e.currentTarget.value)}
                value={message}
                id="text"
              />
              <span className="input-group-btn">
                <button id="submit" type="submit" className="btn btn-primary">
                  Send
                </button>
              </span>
            </div>
          </form>
        </div>
        <div className="col-md-4">
          <h6>Users</h6>
          <ul id="users">
            {users.map(({ name, id }) => (
              <li key={id}>{name}</li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  );
};

ReactDOM.render(<App />, document.getElementById("root"));
